import * as React from "react";
import { graphql } from "gatsby";

interface CdData {
  data: {
    albumsJson: {
      title: string;
      number: string;
      artworks: {
        A: {
          medium: string;
        };
        L1: {
          medium: string;
        };
      };
    };
  };
}

export default (props: CdData) => {
  const cd = props.data.albumsJson;
  console.log(cd);

  return cd ? (
    <div>
      <h1>Cd Page</h1>
      <h2>{cd.title}</h2>
      <img src={cd.artworks.A.medium} width="300px" />
    </div>
  ) : (
    <h1>Cd Page</h1>
  );
};

export const query = graphql`
  query($number: String!) {
    albumsJson(number: { eq: $number }) {
      title
      number
      artworks {
        A {
          medium
        }
        L1 {
          medium
        }
      }
    }
  }
`;
